@media only screen and (max-width: 768px) {
.servies_info{
	list-style: none;
	padding: 0;
	margin: 0;
	width: 100%;
	li{
	    background-image: url(../img/service_shape.png);
	    float: left;
	    padding: 0px 0px;
	    margin: 10% 4px;
	    width: 100%!important;
	    text-align: center;
	    height: 219px;
	    background-repeat: no-repeat;
	    background-position: 50% ,50%;
	    p{
	    	font-family: $alternate_font_family_1;
	    	font-weight: 700;
	    	color: $primary_color;

	    }
	}
}

h1, .h1{
	font-family: $alternate_font_family_1;
	font-size: 32px!important;
	font-weight: 700;
	text-transform: Capitalization;
	line-height: 37px!important;
	letter-spacing: default;
	color: $primary_color;
}

h2, .h2{
	font-family: $alternate_font_family_1;
	font-size: 26px !important;
	font-weight: 600 !important;
	text-transform: Capitalization;
	line-height: 31px !important;
	letter-spacing: default;
	color: $primary_color;
}

h3, .h3{
	font-family: $alternate_font_family_1;
	font-size: 22px !important;
	font-weight: 300 !important;
	text-transform: Capitalization;
	line-height: 25px !important;
	letter-spacing: default;
	color: $secondary_color;
}

h4, .h4{
	font-family: $alternate_font_family_1;
	font-size: 34px !important;
	font-weight: 600 !important;
	text-transform: Capitalization;
	line-height: 36px !important;
	letter-spacing: default;
	color: $primary_color;
}

h5, .h5{
	font-family: $alternate_font_family_1;
	font-size: 32px !important;
	font-weight: 600 !important; 
	text-transform: Capitalization;
	line-height: 37px !important;
	letter-spacing: default;
	color: $white_color;
}

h6, .h6{
	font-family: $alternate_font_family_1;
	font-size: 22px !important;
	font-weight: 700 !important;
	text-transform: Capitalization;
	line-height: 24px !important;
	letter-spacing: default;
	color: $primary_color;
}
 .h7{
	font-family: $default_font_family;
	font-size: 22px !important;
	font-weight: 400 !important;
	text-transform: Capitalization;
	line-height: 24px !important;
	letter-spacing: default;
	color: $primary_color;
}
body, p, .p{
	font-family: $default_font_family;
	font-size: 16px !important;
	font-weight: 400 !important;
	text-transform: none;
	line-height: 26px !important;
	letter-spacing: default;
	color: $quaternary_color !important;

}

.subpage ul {
    font-family: $default_font_family;
	font-size: 16px !important;
	font-weight: 400 !important;
	text-transform: none;
	line-height: 26px !important;
	letter-spacing: default;
	color: $quaternary_color !important;
}



	//Classes for font-weights
	.bc_sm_text_thin { font-weight: 100 !important; }
	.bc_sm_text_xlight { font-weight: 200 !important; }
	.bc_sm_text_light { font-weight: 300 !important; }
	.bc_sm_text_normal { font-weight: 400 !important; }
	.bc_sm_text_medium { font-weight: 500 !important; }
	.bc_sm_text_semibold { font-weight: 600 !important; }
	.bc_sm_text_bold { font-weight: 700 !important; }
	.bc_sm_text_xbold { font-weight: 800 !important; }
	.bc_sm_text_heavy { font-weight: 900 !important; }

	//Classes for font sizes
	.bc_sm_text_16 { font-size: 16px !important; }
	.bc_sm_text_18 { font-size: 18px !important; }
	.bc_sm_text_20 { font-size: 20px !important; }
	.bc_sm_text_24 { font-size: 24px !important; }
	.bc_sm_text_30 { font-size: 30px !important; }
	.bc_sm_text_32 { font-size: 32px !important; }
	.bc_sm_text_36 { font-size: 36px !important; }
	.bc_sm_text_48 { font-size: 48px !important; }

	//Classes for line Heights
	.bc_sm_line_height_14 { line-height: 14px !important; }
	.bc_sm_line_height_16 { line-height: 16px !important; }
	.bc_sm_line_height_18 { line-height: 18px !important; }
	.bc_sm_line_height_20 { line-height: 20px !important; }
	.bc_sm_line_height_22 { line-height: 22px !important; }
	.bc_sm_line_height_24 { line-height: 24px !important; }
	.bc_sm_line_height_26 { line-height: 26px !important; }
	.bc_sm_line_height_30 { line-height: 30px !important; }
	.bc_sm_line_height_33 { line-height: 33px !important; }
	.bc_sm_line_height_34 { line-height: 34px !important; }
	.bc_sm_line_height_36 { line-height: 36px !important; }
	.bc_sm_line_height_37 { line-height: 37px !important; }
	.bc_sm_line_height_40 { line-height: 40px !important; }
	.bc_sm_line_height_48 { line-height: 48px !important; }
	.bc_sm_line_height_60 { line-height: 60px !important; }


	//Classes for letter spacing
	.bc_sm_letter_spacing_24 { letter-spacing: 24px !important; }
	.bc_sm_letter_spacing_25 { letter-spacing: 25px !important; }
	.bc_sm_letter_spacing_36 { letter-spacing: 36px !important; }
	.bg_color_white {background-color: #ffffff !important; }
}
.mobile_anchor{
	a{
		color:$white_color;
		font-weight: 700;
		&:hover{
			text-decoration:none;
		}
	}
}