//Short form with floating labels
.floating_labels_wrapper {
	margin: 0px 0px !important;
	padding-right: 10px !important;
	padding-left: 10px !important;

	.floating_labels{
		label {
			position: absolute;
			margin-top: 20px;
			margin-left: 10px;
			-webkit-transition: all 0.2s ease;
		    -moz-transition: all 0.2s ease;
		    -o-transition: all 0.2s ease;
		    transition: all 0.2s ease;
		    opacity: 1;
			line-height: 36px;
			color:#666666;
			font-size: 12px !important;
			font-weight: 400 !important;
		}

		.gfield{
			padding-top: 10px !important;
			margin-top: 0px !important;
			padding-bottom: 0px !important;
			margin-bottom: 0px !important;
			input::placeholder{
				color:transparent;
			}
			select.gfield_select{
				padding: 8px !important;
				margin-top: 0px !important;
				padding-top: 18px !important;
				padding-bottom: 18px !important;
			}
		}

		ul.gfield_checkbox{
			li{
				overflow: visible;
			}	
		} 
	}

	.gform_footer{
		padding: 12px !important;
		margin: 0px 0px !important;
		input {
			margin-bottom: 10px !important;
		}
	}
}


.fixed_labels_wrapper{

	.gform_heading{
		display:block !important;
	}
	.gform_title{
		font-family: $alternate_font_family_1;
		font-size: 26pt !important;
		text-align: left;
		color: #00395e;
	}

	
	ul li.gfield {
		padding-left:0px !important;
		padding-right:0px !important;
		margin-top: 0px !important;
		
		
		input{
			padding: 10px 5px !important;
			border-radius: 6px;
			border: solid;
			border-width: 1px;
			border-color: #ccc;
			margin-bottom: 15px;
		}

		label{
			color: #4d4d4d;
			font-size: 14px !important;
			margin: 0px;
		}

		select.gfield_select.large{
			padding: 19px !important;
			border-radius: 6px;
			border: solid;
			border-width: 1px;
			border-color: #ccc;
			margin-bottom: 15px;
		}


	}

	&.gform_wrapper.gform_validation_error{
		.gform_body ul li.gfield.gfield_error{
			&:not(.gf_left_half):not(.gf_right_half){
				max-width: 100% !important;
			}
			label{
				color: #4d4d4d;
				font-size: 14px !important;
				margin: 0px;
			}	
		}
	}

	.gform_footer {
		padding: 0px !important;
		margin: 0px 0px !important;
		text-align: left;
		.gform_button {
			width: 50%;
			min-width: 40px;
		}
	}
}


.bc_contact_form_full { 
