.gform_wrapper {

	.validation_error{
		display: none;
	}

	ul li.gfield.gfield_error{
		margin-bottom: 0px !important;
		background-color:rgba(255,223,224,0) !important;
		border:none !important;

		::placeholder{
			color: #ffffff;
		}

		label{
			color: #ffffff;
			font-size: 14px !important;
			margin: 0px;
		}

		select, input {
			color: #ffffff;
			border-color: #eb0606 !important;
		}
	}


	.validation_message{
		margin-top: -64px;
		margin-left: 10px;
		pointer-events: none;
		color: #eb0606 !important;
		&--float{
			@extend .validation_message;
			margin-top: -65px;
			font-size: 12px !important;
			position: absolute;
			padding: 0px 0px !important;
		}
	}


	.gform_description{
		line-height: 32px;
	}

	.gform_footer{
		padding: 12px !important;
		margin: 0px 0px !important;
		text-align: center;
	}

	.gform_heading{
		display:none;
	}

	.gform_title{
		text-align:center;
		color:$bc_contact_form_label_color;
	}
	.top_label{
		div.ginput_container {
			margin-top:0px !important;;
		}
	}
	ul{

		li.gfield{

			select {
				font-size: 12px;
				// background:#ffffff;
				margin-top: 12px;
				background: url('../img/br_down.png') no-repeat right #ffffff !important;
				-webkit-appearance: none !important;
				-moz-appearance:none; /* Firefox */
				appearance:none;
				background-position: right 10px center !important;
			}
			::placeholder{
				opacity: 1;
				font-size: 14px;
			}
			.large, .medium{
				padding:map-get($form_input, padding);
				border:{
					radius: map-get($form_input, border-radius);
					style: solid;
					width: 1px;
					color: #c1c1c1;
				}
			}
			@media only screen and (min-width: 768px) {
				padding-left: 16px;
			}
		}
	}
}
